  height: 30px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #000000;
  margin: 1% 1%;
}

.products-search-container {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}

